.work_block {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  color: #fff;
  min-height: 300px;
  overflow: hidden;
  margin: 5px;
}

.work_link {
  width: 100%;
  min-height: 300px;
  display: block;
  text-align: right;
  font-family: 'Lato';
  text-decoration: none;
  position: relative;
}

.work_link span {
  display: block;
  padding: 120px 5px 0 0;
}

.work_link span h3 {
  text-transform: uppercase;
  display: block;
  opacity: 0;
  margin: 0 90% 0 0;
  color: #434d83;
  text-shadow: 1px 1px 1px #fff;
  -webkit-transition: 1s;
  transition: 1s;
}

.work_cover {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0 0 0 0;
  background-color: #aab5ca;
  color: #fff;
  opacity: 0.8;
  -webkit-transition: 1s;
  transition: 1s;
  cursor: pointer;
}

.work_cover span {
  display: block;
  padding: 120px 20px 0 20px;
  text-align: center;
}

.work_block:hover .work_cover {
  margin: 0 90% 0 -90%;
  -webkit-transition: 1s;
  transition: 1s;
}

.work_block:hover .work_link span h3 {
  opacity: 1;
  margin: 0 0 0 0;
  -webkit-transition: 1s;
  transition: 1s;
}
